@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap);
.navbar {
    width: 100%;
    padding: .5rem 1rem;

    text-align: center;
}

.nav-wordmark {
    max-height: 80px;
}

.main-nav {
    margin: 0 1rem;
    transition: text-shadow .2s;

    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    text-decoration: none;
    
    color: var(--white) !important;
}

.main-nav:hover {
    text-shadow: 0px 0px 20px var(--light-grey);
}

.main-nav.active {
    text-shadow: 0px 0px 20px var(--light-grey);
    font-style: italic;
}
.footer {
    position: absolute;
    width: 100%;
    padding: 1rem 0;
    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;

    background: linear-gradient(180deg, var(--black) 0%, var(--dark-grey) 100%);
    color: var(--light-grey);
}

.col {
    align-items: center;
    align-content: center;
}

.col-logo {
    display: block;
    justify-content: right;
    text-align: end;
    margin-right: 1rem;
}

.footer-logo {
    max-height: 150px;
}

.col-contact {
    padding-top: 2rem;
}

.footer-icon {
    margin-right: 1rem;

    color: var(--light-grey);
}

.footer-icon:hover {
    box-shadow: 0px 0px 20px var(--light-grey);
}

@media only screen and (max-width: 640px) {
    .footer {
        font-size: 16px;
    }
}
.home {
    text-align: center;
    height: 100vh;
    width: 100vw;

    background: linear-gradient(180deg, var(--dark-grey) 0%, var(--black) 90%);
}

.logo {
    padding: 2% 0;
    max-height: 80vh;
    max-width: 80vw;
}

.text-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.greeting-container {
    position: absolute;
    top: 50%;
    height: 18vw;
    overflow: hidden;

    padding: 0px 20px;
}

.greeting {
    display: block;
    -webkit-animation: spin_words 5s infinite;
            animation: spin_words 5s infinite;
    margin: 2px 2px;

    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;
    font-size: 12vw;

    color: var(--white);
    text-shadow: 0px 0px 8px var(--white);
}

@media only screen and (max-height: 800px) {
    img {
        max-height: 800px;
    }
}

@-webkit-keyframes spin_words {
    20% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    40% {
        -webkit-transform: translateY(-200%);
                transform: translateY(-200%);
    }
    60% {
        -webkit-transform: translateY(-300%);
                transform: translateY(-300%);
    }
    80% {
        -webkit-transform: translateY(-400%);
                transform: translateY(-400%);
    }
    100% {
        -webkit-transform: translateY(400%);
                transform: translateY(400%);
    }
}

@keyframes spin_words {
    20% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    40% {
        -webkit-transform: translateY(-200%);
                transform: translateY(-200%);
    }
    60% {
        -webkit-transform: translateY(-300%);
                transform: translateY(-300%);
    }
    80% {
        -webkit-transform: translateY(-400%);
                transform: translateY(-400%);
    }
    100% {
        -webkit-transform: translateY(400%);
                transform: translateY(400%);
    }
}
html {
    background: var(--dark-grey);
}

.page {    
    font-family: Cormorant Infant;
    font-style: normal;
    font-weight: 600;

    text-shadow: 0px 0px 20px var(--black);

    background: var(--black);
    color: var(--white);
}

.page p {
    font-family: Quicksand;
    font-style: normal;
    font-weight: 500;
}

a:hover {
    cursor: pointer;
}

.carousel-caption {
    background: linear-gradient(
        90deg, 
        transparent, 
        var(--black), 
        transparent);
    
    transition: text-shadow .5s;
}

.carousel-img {
    border-radius: 100px;
    max-height: 80vh;

    transition: -webkit-transform .5s;

    transition: transform .5s;

    transition: transform .5s, -webkit-transform .5s;
}

.carousel-img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.carousel-img:hover + .carousel-caption {
    text-shadow: 0px 0px 30px var(--white);
}

.modal {
    color: var(--black);
}

.modal-header {
    width: 100%;
    text-align: center;
    background-size: cover;

    -webkit-filter: grayscale(50%);

            filter: grayscale(50%);
    box-shadow: inset 0 0 8rem 1rem black;
}

.modal-title {
    width: 100%;
    font-family: Cormorant Infant;
    background: linear-gradient(90deg, transparent, var(--white), var(--white), transparent);
}

.modal-body {
    font-family: Quicksand;
    font-size: 18px;

    background: var(--white);
}

@media only screen and (max-width: 991px) {
    .carousel-caption {
        background: rgb(0, 10, 20, .5);
    }
}
.card {
    margin: 2.5vw;
    border-color: var(--dark-grey) !important;
    transition: all .5s;

    -webkit-filter: grayscale(50%);

            filter: grayscale(50%);
}

.card:hover {
    text-shadow: 0px 0px 10px var(--white);
    box-shadow: 0px 0px 2.5px 2.5px var(--light-grey);

    -webkit-transform: scale(1.1);

            transform: scale(1.1);

    -webkit-filter: none;

            filter: none;
}

.card-img {
    padding: 0;
}

.card-img-overlay {
    background-image: linear-gradient(
        0deg,
        transparent 20%,
        var(--black) 80%);
    
    text-align: center;
}

.card-title, .card-text {
    color: var(--white);
}

.source {
    margin-top: 10px;
}
.biodata-col {
    padding: 1rem 4rem;
}

.biodata-content {
    font-size: 120%;
    padding: 0 1rem;
    color: var(--white);
}

.biodata-row {
    justify-content: center;
    align-items: center;
}

.biodata-rowName {
    color: var(--light-grey);
}
:root {
    --black: #000A14;
    --dark-grey: #36404A;
    --grey: #76808A;
    --light-grey: #B5BFC9;
    --white: #EBF5FF;
}

@font-face {
    font-family: Pinyon Script, Garamond, serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}

@font-face {
    font-family: Cormorant Infant, Garamond, serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}

@font-face {
    font-family: Quicksand, Verdana, Geneva, Tahoma, sans-serif;
    src: url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@600&family=Pinyon+Script&family=Quicksand:wght@400;700&display=swap');
}
